a, a:visited {
  color: #326ce5;
  text-decoration: none; }

.charts_container {
  margin-top: 5px;
  margin-bottom: 25px; }

.charts {
  display: flex;
  justify-content: space-between;
  text-align: center; }

.charts_item {
  color: #888098;
  box-shadow: 0 0 5px 0 #AAA;
  background-color: #FFF;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  flex: 1;
  margin: 15px;
  padding: 10px; }

.charts_item:first-child {
  margin-left: 0; }

.charts_item:last-child {
  margin-right: 0; }

.charts_chart {
  flex: 1; }

.charts_donut {
  display: grid;
  justify-content: center;
  align-items: center; }

.charts_number {
  font-size: 100px; }

.chart_donutLabel {
  grid-column: 1;
  grid-row: 1;
  font-size: 16pt; }

.charts_itemLabel {
  font-size: 13pt;
  color: #AAA;
  margin-top: 8px;
  text-align: right;
  text-transform: uppercase; }

.charts_itemSubLabel {
  color: #AAA;
  font-size: 7pt;
  text-transform: uppercase; }

.chart_innerLabel {
  color: #AAA;
  font-size: 7pt; }

.chart_dots {
  display: none; }

.chart_dotSelected {
  color: #AAA; }

@media only screen and (max-width: 812px) {
  .chart_donutLabel {
    font-size: 10pt; }
  .charts_itemLabel {
    font-size: 10pt; } }

@media only screen and (max-width: 700px) {
  .charts {
    flex-wrap: nowrap;
    overflow-x: hidden; }
  .chart_1 > .charts_item {
    transform: translateX(calc(-1 * (100vw - 16px))); }
  .chart_2 > .charts_item {
    transform: translateX(calc(-2 * (100vw - 16px))); }
  .chart_3 > .charts_item {
    transform: translateX(calc(-3 * (100vw - 16px))); }
  .charts_item {
    margin: 10px;
    flex: 0 0 calc(100vw - 36px);
    transition-duration: 0.25s;
    transition-property: transform; }
  .charts_item:first-child {
    margin-left: 3px; }
  .charts_item:last-child {
    margin-right: 3px; }
  .chart_dots {
    display: block;
    text-align: center; } }

.donut {
  width: 150px;
  height: 150px;
  grid-column: 1;
  grid-row: 1;
  pointer-events: none; }

.donut_layer, .donut_background, .donut_layer1, .donut_layer2 {
  fill: transparent;
  stroke-width: 6; }

.donut_background {
  stroke: #AAA; }

.donut_layer1 {
  stroke: #326ce5; }

.donut_layer2 {
  stroke: #B9314F; }

.contentPanel {
  box-shadow: 0 0 5px 0 #AAA;
  margin-bottom: 35px;
  padding: 0 20px 10px 20px;
  overflow: scroll; }

.contentPanel_header {
  display: none; }

.contentPanel_label {
  margin-right: 5px; }

.contentPanel_warn {
  color: #B9314F;
  font-weight: bold; }

@media only screen and (max-width: 700px) {
  .contentPanel {
    box-shadow: none;
    padding: 0; }
  .contentPanel_header {
    display: block;
    position: relative;
    padding: 5px;
    font-size: 13pt;
    text-align: right;
    text-transform: uppercase;
    color: #888098;
    background-color: #F0F0F0; } }

#header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  margin-bottom: 25px; }

#header > .button_headerAction {
  transform: translateY(-80px);
  transition-duration: 0.25s;
  transition-property: transform; }

#header > .button_headerAction > svg {
  transform: rotate(180deg);
  transition-duration: 0.25s;
  transition-property: transform; }

#header.header_ready > .button_headerAction {
  transform: translateY(0); }

#header > .button_headerAction > svg {
  transform: rotate(0); }

.header_label {
  font-size: 16pt; }

.header_fill {
  flex: 1; }

input[type="text"].header_filter {
  border: none;
  margin: 0 10px;
  padding: .25rem 0;
  text-align: right;
  transition-duration: 0.25s;
  transition-property: border-color, padding; }
  input[type="text"].header_filter:hover {
    padding: .25rem; }
  input[type="text"].header_filter:focus {
    padding: .25rem;
    border-color: #AAA; }

#header .button_headerAction:nth-child(1) {
  transition-delay: 0.1s; }

#header .button_headerAction:nth-child(2) {
  transition-delay: 0.2s; }

#header .button_headerAction:nth-child(3) {
  transition-delay: 0.3s; }

#header .button_headerAction:nth-child(4) {
  transition-delay: 0.4s; }

#header .button_headerAction:nth-child(5) {
  transition-delay: 0.5s; }

#header .button_headerAction:nth-child(6) {
  transition-delay: 0.6s; }

#header .button_headerAction:nth-child(7) {
  transition-delay: 0.7s; }

#header .button_headerAction:nth-child(8) {
  transition-delay: 0.8s; }

#header .button_headerAction:nth-child(9) {
  transition-delay: 0.9s; }

@media only screen and (max-width: 700px) {
  .header_label {
    font-size: 13pt; }
  #header {
    margin-bottom: 5px; } }

.modal_overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.33); }

.modal_modal {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  background: #FFF;
  outline: none;
  padding: 20px;
  margin-right: -50%;
  transform: translate(-50%, -50%); }

.modal_actions {
  display: flex;
  justify-content: flex-end;
  align-items: center; }

@media only screen and (max-width: 700px) {
  .modal_overlay {
    z-index: 300; }
  .modal_modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0;
    transform: initial; }
  .modal_actions {
    margin-bottom: 60px; } }

.select_namespace {
  margin-left: 15px;
  display: inline-block;
  width: 155px; }

div.react-select__control {
  border: 1px solid #F0F0F0;
  border-radius: 0;
  min-height: initial; }

div.react-select__control:hover {
  border-color: #AAA;
  box-shadow: none; }

div.react-select__indicator {
  padding: 0; }

div.react-select__menu {
  border-radius: 0; }

div.react-select__option {
  background-color: transparent; }

div.react-select__option:hover {
  background-color: #F0F0F0; }

div.react-select__option--is-selected {
  color: #326ce5;
  background-color: #F0F0F0; }

span.react-select__indicator-separator {
  display: none; }

svg {
  fill: #326ce5;
  width: 25px;
  height: 25px; }

.svg_error {
  fill: #B9314F; }

.svg_button {
  cursor: pointer; }

table {
  width: 100%;
  border-spacing: 3px; }

thead > tr > th {
  color: #AAA;
  font-weight: normal;
  text-transform: uppercase;
  text-align: left;
  padding: 0.75rem;
  border-bottom: 1px solid #AAA; }

td {
  padding: 0.75rem; }

.th_icon {
  width: 20px; }

.td_icon {
  text-align: center; }

.td_iconLabel {
  font-size: 7pt;
  margin-top: -2px;
  text-transform: uppercase; }

@media only screen and (max-width: 700px) {
  thead > tr > th {
    padding: 0.75rem 0.25rem; }
  td {
    padding: 0.75rem 0.25rem; }
  .wrapped > thead {
    display: none; }
  .wrapped > tbody > tr {
    display: flex;
    flex-direction: column;
    margin-bottom: 35px; }
  .wrapped > tbody > tr > td {
    padding: 0.25rem; }
  .wrapped .wrapped_name {
    font-weight: bold; } }

textarea {
  white-space: pre;
  color: #344055;
  background: transparent;
  padding: .25rem 0;
  border: 1px solid transparent;
  resize: none;
  outline: none;
  border-color: #F0F0F0;
  transition-duration: 0.25s;
  transition-property: border-color, padding; }
  textarea:focus {
    outline: none !important;
    border-color: #AAA; }

input[type="text"], input[type="number"], input[type="password"] {
  outline: none !important;
  color: #344055;
  background: transparent;
  border: 1px solid transparent;
  border-color: #F0F0F0; }

html, body, div, input, select {
  box-sizing: border-box; }

html, body, #root {
  min-height: 100vh; }

html {
  font-family: sans-serif;
  font-size: 10pt;
  font-weight: 300;
  color: #344055;
  min-width: 320px; }

#root {
  display: flex;
  flex-direction: column; }

#shell {
  display: flex;
  min-height: calc(100vh - 55px); }

#menu {
  background-color: #F0F0F0;
  padding: 8px; }

#content {
  padding: 0 45px;
  flex: 1;
  width: calc(100vw - 72px); }

#titleBar {
  height: 55px;
  background-color: #326ce5;
  color: #FFF;
  display: flex;
  align-items: center;
  padding: 0 8px;
  font-size: 16pt;
  justify-content: space-between;
  z-index: 1; }

.titleBar_logo {
  width: 80px;
  height: 80px; }

button.titleBar_hamburger {
  display: none; }

.titleBar_hamburger > svg {
  fill: #FFF; }

.smallText {
  color: #AAA;
  font-size: 7pt; }

@media only screen and (max-width: 1200px) {
  .optional_medium {
    display: none; } }

@media only screen and (max-width: 812px) {
  .optional_small {
    display: none; }
  #menu {
    width: 44px; }
  #content {
    width: calc(100vw - 44px); } }

@media only screen and (max-width: 700px) {
  #content {
    padding: 0 15px;
    width: 100vw; }
  .optional_xsmall {
    display: none; }
  #menu {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 200;
    width: 72px;
    overflow-y: scroll;
    box-shadow: 0 0 5px 0 #AAA;
    transform: translateX(-80px);
    transition: transform 0.33s; }
  #menu.menu_toggled {
    transform: translateX(0); }
  button.titleBar_hamburger {
    display: initial; } }
